.services{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  &__item{
    overflow: hidden;
    flex-basis: 50%;
    width: 50%;
    padding-right: 3.75rem;
    margin-bottom: 4rem;
    position: relative;
    @include media('<md'){
      margin-bottom: 2rem;
    }
    @include media('<sm'){
      width: 100%;
      flex-basis: 100%;
      padding-right: 0;
    }
  }
  &__inner{
    position: relative;
    @include media('<sm'){
      padding-left: 60px;
    }
  }
  &__icon{
    fill: $pink;
    display: inline-block;
    font-size: 52px;
    // width: 52px;
    // height: 52px;
    line-height: 0;
    margin-bottom: 1.25rem;
    @include media('<xs'){
      font-size: 40px;
      // width: 40px;
      // height: 40px;
    }
    @include media('<sm'){
      font-size: 38px;
      position: absolute;
      left: 0;
      top: 0;
      // width: 38px;
      // height: 38px;
    }
    @include media('<lg'){
      font-size: 48px;
      // width: 48px;
      // height: 48px;
    }
    > svg{
      display: inline-block;
      vertical-align: middle;
      font-size: 1em;
      height: 1em;
      width: 1em;
      fill: inherit;
      stroke: inherit;
    }
  }
  &__title{
    color: $primary;
    font-weight: font-weight(normal);
    margin-bottom: 1rem;
    @include media('<xs'){
      font-size: 1.25rem;
      margin-bottom: 0.5rem;
    }
    @include media('<md'){
      margin-bottom: 0.625rem;
    }
    a{
      color: $pink;
      &:hover{
        text-decoration: none;
      }
    }
  }
  &__body{
    line-height: line-height(lg);
  }
}